import React, { useState } from "react";
import { Navbar, NavItem, NavLink, Nav } from "reactstrap";
import Scrollspy from "react-scrollspy";
import './Navbar.scss';

export default function NavbarComponent() {
  const [curr, setCurr] = useState(0);
  return (
    <div className="navbar-wrapper">
      <Navbar className="navbar-vertical">
        <Nav>
          <Scrollspy
            componentTag="div"
            items={[
              "section-0",
              "section-1",
              "section-2",
              "section-3",
              "section-4",
              "section-5",
            ]}
            currentClassName="navbarCurrent"
            onUpdate={(e) => setCurr(e.id.split("-")[1])}
          >
            <NavItem>
              <NavLink
                href="#Home"
              >
                <span className="nav-item-icon">
                  <i className="ni ni-box-2" />
                </span>
                <span>Home</span>
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink
                href="#About"
              >
                <span className="nav-item-icon">
                  <i className="ni ni-single-02" />
                </span>
                <span>About</span>
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink
                href="#Background"
              >
                <span className="nav-item-icon">
                  <i className="ni ni-collection" />
                </span>
                <span>Background</span>
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink
                href="#Skills"
              >
                <span className="nav-item-icon">
                  <i className="ni ni-spaceship" />
                </span>
                <span>Skills</span>
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink
                href="#Projects"
              >
                <span className="nav-item-icon">
                  <i className="ni ni-vector" />
                </span>
                <span>Projects</span>
              </NavLink>
            </NavItem>
          </Scrollspy>
        </Nav>
      </Navbar>
    </div>
  );
}
