import React, { useEffect, useState } from "react";
import Particles from "react-particles-js";


import "./Home.scss";

export default function Home() {
  const [data, setData] = useState(null);
  useEffect(() => {
    fetch("assets/data/home.json")
      .then((resp) => resp.json())
      .then((res) => {
        if (res) {
          setData(res);
        }
      });
  }, []);
  if (!data) {
    return <div></div>;
  }
  return (
    <div id="Home">
      <div className="background">
        <Particles
          className="particles"
          width="100vw"
          height="100vh"
          params={{
            fps_limit: 32,
            particles: {
              collisions: {
                enable: true,
              },
              number: {
                value: 220,
                density: {
                  enable: true,
                },
              },
              color: {
                value: "rgba(50,50,50,0.25)",
              },
              line_linked: {
                enable: true,
                distance: 100,
                opacity: 0.5,
                color: {
                  value: "rgba(150,150,150,0.75)",
                },
              },
              move: {
                speed: 1.25,
              },
              opacity: {
                anim: {
                  enable: true,
                  opacity_min: 0.05,
                  speed: 0.5,
                  sync: false,
                },
                value: 0.5,
              },
            },
            polygon: {
              enable: false,
              scale: 1.5,
              type: "inline",
              move: {
                radius: 10,
              },
              url: "assets/img/shape.svg",
              inline: {
                arrangement: "equidistant",
              },
              draw: {
                enable: true,
                stroke: {
                  width: 3,
                  color: "rgba(0, 0, 0, .2)",
                },
              },
            },
            retina_detect: true,
            interactivity: {
              events: {
                onhover: {
                  enable: false,
                  mode: "grab",
                },
              },
            },
          }}
        />
        <div className="details">
          <img className="prof-avatar" alt="" src={`assets/img/${data.avatar}`} />
          <div className="name h1 display-1 text-light">{data.name}</div>
          <div className="job h4 display-4"><code className="text-danger">{data.job}</code></div>
          <div className="mt-3"><button disabled className="btn btn-outline-info">Download CV</button></div>
          <hr />
        </div>
        <div className="lastupdate h6 display-6 text-light">
          <i className="far fa-calendar" />
          <span className="pr-2 pl-2">Last Update: </span>
          {data.lastupdate}
        </div>
      </div>
    </div>
  );
}
